.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.h4 {
	font-size: larger;
	font-weight: bold;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.outline-card {
	filter: unset;
	border: 1px solid #ccc;
}

.image-button-icon {
	width: 20px;
	height: 20px;
}

.menu-bar {
	gap: 10px;
}

.menu-button {
	display: flex;
	background-color: transparent;
	height: 30px;
	line-height: unset;
	font-size: 20px;
}

.collapsed>div>div {
	color: transparent
}

.side-bar.collapsed>.menu-button.selected>img, .side-bar>.menu-button.selected>img {
	
}

.menu-button>img {
	margin-left: 6px;
	margin-top: 2.5px;
	margin-right: 3px;
}
