#title-action-bar {
	padding: 10px;
}

#device-log-card {
	min-height: 70vh;
	max-height: 70vh;
	min-width: 55vw;
	max-width: 55vw;
	overflow: hidden;
}
#device-log-card>.card-menubar {
	position: relative;
}
#device-log-card>.card-content {
	padding: 0px;
	min-height: calc(100% - 40px) !important;
	max-height: calc(100% - 40px) !important;
	min-width: 100%;
	max-width: 100%;
}

/*Actions card*/
#log-actions-card {
	min-height: fit-content !important;
	max-height: fit-content !important;
	width: 55vw;
	margin-bottom: 10px;
}
#log-actions-card>.card-content {
	display: flex;
	gap: 10px;
	padding: 0px;
}

#add-device-button {
	width: 100px;
	background-color: rgb(193, 95, 206);
	color: rgb(254, 244, 255);
	font-weight: 600;
}
#add-device-button:hover {
	background-color: rgb(154, 72, 165);
}
#add-device-cont {
	border-right: 1px solid #ccc;
	padding-top: 10px;
	padding-bottom: 10px;
	padding-left: 10px;
	padding-right: 10px;
}

#user-select-drop-cont {
	width: 250px;
	display: grid;
	grid-template-columns: 25% 70%;
	gap: 5%;
	align-items: center;
	padding: 10px;
}
#user-select-drop-cont>.form-info-title {
	font-size: larger;
	font-weight: 600;
}

/*Table customization*/
#device-log-card-card-content>.data-table {
	padding: 0px;
}
#device-log-card-card-content>.tablerow.header,
#device-log-card-card-content>.tablerow.header>div  {
	padding: 0px;
	margin: 0px;
	background-color: transparent;
}
#device-log-card-card-content>.data-table>.tablerow,
#device-log-card-card-content>.tablerow.header {
	align-items: center;
	justify-content: center;
}

#device-log-card-card-content>.tablerow.header {
	background-color: #f9f9f9;
	border-bottom: 1px solid #ccc;
	height: 30px;
	line-height: 30px;
	font-size: 20px;
	font-weight: 600;
}
#device-log-card-card-content>.tablerow.header>:last-child {
	border-right: 0px;
}

#device-log-card-card-content>.data-table>.tablerow {
	font-size: 25px;
	height: 50px;
}
#device-log-card-card-content>.data-table>.tablerow>.action-row {
	align-items: center;
	justify-content: center
}
#device-log-card-card-content>.data-table>.tablerow>.action-row>.action-button {
	width: 50%;
}
#device-log-card-card-content>.data-table {
	min-height: calc(100% - 20px) !important;
	max-height: calc(100% - 20px) !important;
	height: calc(70vh - 60px) !important;
	scrollbar-gutter: auto;
	overflow-y: auto;
}

#device-log-card-card-content>.data-table>.tablerow {
	border-bottom: 1px solid #ccc;
	padding: 0px;
}
#device-log-card-card-content>.data-table>.tablerow:hover {
	background-color: #f0f0f0;
}

#device-log-card-card-content>.data-table>.tablerow>div {
	padding: 0px;
	margin: 0px !important;
	height: 100%;
	line-height: 50px;
}

#device-log-card-card-content>.tablerow.header>div {
	border-right: 1px solid #ccc;
}