#device-editor {
	width: 60vw;
	min-height: fit-content;
	max-height: fit-content;
}
#device-editor>.card-content {
	height: calc(100% - 40px);
	display: flex;
	flex-direction: column;
	gap: 10px;
}

#dev-editor-user-info {
	text-align: left;
}

.device-editor-form {
	border: 1px solid #ccc;
	background-color: #f9f9f9;
	border-radius: 10px;
	display: grid;
	grid-template-columns: 33% 34% 33%;
	row-gap: 10px;
}
.device-editor-form>.form-info-item {
	display: grid;
	grid-template-columns: 40% 60%;
	padding: 10px;
	align-items: center;
}
.device-editor-form>.form-info-item>input {
	width: 100%;
	height: 100%;
	padding: 10px;
}
.device-editor-form>.form-info-item>.form-info-title {
	font-size: 20px;
	font-weight: 550;
	color: #212121;
}